import React from "react";
import { navigate, type PageProps } from "gatsby";
import Header from "../header/Header";
import Footer from "../footer/Footer";
import EventHero from "../EventHero/EventHero";
import EventSection from "../EventAbout/EventAbout";
import SponsorsSection from "../SponsorsSection/SponsorsSection";
import TestimonialsSection from "../TestimonialsSection";
import EventSpeakers from "../EventSpeakers/EventSpeakers";
import PriceList from "../PriceList/PriceList";
import EventAttendees from "../EventAttendees/EventAttendees";
import { useLanguage } from "../../context/LanguageContext";
import { getContent } from "../../content/content";

import { useLocation } from "@reach/router";

const EventPage = (props?: PageProps) => {
  const { lang } = useLanguage();
  const content = getContent(lang).pages.landing.section.eventContent;

  const eventData = {
    eventName: "PHP CONFERENCE IN ISTANBUL",
    eventDate: "2025-12-25T09:00:00",
    location: "3200 Barbaros Bulvan Besiktas/Istanbul, TR",
    remainingTickets: 245,
    speakersCount: 24,
  };

    const url = useLocation();
    const eventSlug = url.pathname.split("/")[2];

  const handleScrollToPricing = () => {
    const el = document.getElementById("pricing");
    if (el) el.scrollIntoView({ behavior: "smooth" });
  };

  // âœ… Optional use of Gatsby props only when present
  //const path = props?.path;
  //const location = props?.location;

  const onBuyButton = () => navigate(`/event/${eventSlug}/checkout`)
  return (
    <main style={{ background: "#1A202C", minHeight: "100vh" }}>
      <Header />
      <EventHero
        eventName={eventData.eventName}
        eventDate={eventData.eventDate}
        location={eventData.location}
        remainingTickets={eventData.remainingTickets}
        speakersCount={eventData.speakersCount}
        onRegisterClick={handleScrollToPricing}
      />
      <EventSection />
      <EventSpeakers />
      <SponsorsSection />
      <TestimonialsSection />
      <PriceList pricing={[
        { name: "Personal", price: 111, features: ["Event Entry", "Free Lunch", "Certificate"], onBuyButton },
        { name: "Pro", price: 299, features: ["Everything in Personal", "Workshop Access", "VIP Networking"], onBuyButton },
        { name: "Enterprise", price: 599, features: ["Everything in Professional", "Exhibition Booth", "Speaking Opportunity"], onBuyButton},
      ]}/>
      <EventAttendees />
      <Footer />
    </main>
  );
};

export default EventPage;

export const Head = () => (
  <>
    <title>PHP Conference Istanbul 2024 - Event Registration</title>
    <meta
      name="description"
      content="Join PHP Conference Istanbul 2024 featuring industry leaders and professional speakers."
    />
  </>
);
